@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@400;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Caveat:wght@700&display=swap);
body {
  font-family: "Mulish", sans-serif;
  margin: 0;
  color: rgb(77, 77, 77);
}

.text-center {
  text-align: center;
}

.container {
  width: 90%;
  margin: 0 auto;
}

#footer {
  width: 100%;
  margin: 0 auto;
  text-align: center;
  margin-top: 40px;
}

#footer a {
  text-decoration: none;
  color: black;
}

#footer a:hover {
  cursor: pointer;
  font-weight: bold;
}

.autoestimeitor {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 30px;
  margin-bottom: 0;
  color: rgb(125, 42, 232);
  font-family: "Caveat", cursive;
  font-size: 20px;
}

.container-login {
  background-color: rgb(226, 226, 226);
  width: 90%;
  max-width: 800px;
  margin: 0 auto 30px auto;
  border-radius: 10px;
  padding: 30px 0;
  margin-top: 10px;
  box-shadow: 9px 10px 13px -1px rgba(110, 110, 110, 0.5);
  -webkit-box-shadow: 9px 10px 13px -1px rgba(110, 110, 110, 0.5);
  -moz-box-shadow: 9px 10px 13px -1px rgba(110, 110, 110, 0.5);
}
h2,
h3 {
  margin: 0;
}
p {
  margin: 0 auto;
}
img {
  height: 20px;
  margin-left: 10px;
}
.buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 10px;
}
.login-btn {
  background-color: white;
  width: 80%;
  margin: 10px 0;
  padding: 10px 0;
  border: none;
  border-radius: 5px;
  font-family: "Mulish", sans-serif;
  display: flex;
  border: 1px solid rgb(180, 179, 179);
}
.login-btn:hover {
  background-color: rgb(228, 222, 222);
  cursor: pointer;
}
.login-email {
  display: flex;
  flex-direction: column;
  height: 100px;
  justify-content: space-evenly;
  width: 80%;
}

h3 {
  margin: 10px 0;
}
input {
  width: 100%;
  border: none;
  height: 30px;
  background-color: white;
  text-align: left;
  border-radius: 5px;
  border: 1px solid rgb(180, 179, 179);
}
input:hover {
  background-color: rgb(228, 222, 222);
}
@media (min-width: 768px) {
  .login-email {
    flex-direction: row;
    height: 30px;
    width: 100%;
  }
  input {
    width: 35%;
  }
}
.btns {
  display: flex;
  flex-direction: column;
}

.btn {
  padding: 10px 50px;
  margin-top: 20px;
  border: none;
  background-color: rgb(125, 42, 232);
  color: white;
  border-radius: 5px;
}

.btn:hover {
  background-color: rgba(124, 42, 232, 0.938);
}

nav {
  background-color: rgb(226, 226, 226);
  width: 100vw;
  box-shadow: 2px 10px 20px -8px rgba(0, 0, 0, 0.51);
  -webkit-box-shadow: 2px 10px 20px -8px rgba(0, 0, 0, 0.51);
  -moz-box-shadow: 2px 10px 20px -8px rgba(0, 0, 0, 0.51);
}
.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav img {
  height: 50px;
  color: red;
}

.nav .btn {
  margin: 0;
  padding: 10px 30px;
}

.photo-start {
  margin-top: 50px;
  text-align: center;
}

.photo-user {
  height: 200px;
  border-radius: 50%;
  margin-top: 20px;
}

.h3 {
  opacity: 0;
}

.loading {
  text-align: center;
  margin-top: 300px;
}

.btn-game:active {
  padding: 12px 52px;
}

.game {
  margin-top: 50px;
  text-align: center;
  position: relative;
}

.circle {
  height: 50px;
}
@media (min-width: 768px) {
  .circle {
    height: 100px;
    margin: 0;
  }
}
.circle-2 {
  position: absolute;
  z-index: 999;
  left: 0;
}
.circle-1 {
  position: absolute;
  left: 42%;
  right: 0;
  text-align: center;
  -webkit-animation: rotation 4s infinite linear;
          animation: rotation 4s infinite linear;
}
@-webkit-keyframes rotation {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}
@keyframes rotation {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}
.comand-btns {
  text-align: center;
  display: flex;
  flex-direction: column;
  width: 50%;
  margin: 250px auto 50px auto;
}

@media (min-width: 768px) {
  .comand-btns {
    flex-direction: row;
    justify-content: space-evenly;
  }
}
.start {
  margin-top: 30px;
}

.continuar {
  text-align: center;
  margin-bottom: 10px;
}

.container-scores {
  width: 80%;
  max-width: 400px;
  margin: 0 auto;
  background-color: rgb(228, 222, 222);
  padding: 10px;
  border-radius: 10px;
  box-shadow: 9px 10px 13px -1px rgba(110, 110, 110, 0.5);
  -webkit-box-shadow: 9px 10px 13px -1px rgba(110, 110, 110, 0.5);
  -moz-box-shadow: 9px 10px 13px -1px rgba(110, 110, 110, 0.5);
  margin-bottom: 50px;
}

.scores-title {
  text-align: center;
  margin-bottom: 10px;
}

.message-page {
  margin-top: 30px;
}
.container-msg {
  width: 90%;
  max-width: 800px;
  margin: 0 auto;
  background-color: rgb(228, 222, 222);
  padding: 10px;
  border-radius: 10px;
  box-shadow: 9px 10px 13px -1px rgba(110, 110, 110, 0.5);
  -webkit-box-shadow: 9px 10px 13px -1px rgba(110, 110, 110, 0.5);
  -moz-box-shadow: 9px 10px 13px -1px rgba(110, 110, 110, 0.5);
  margin-bottom: 50px;
}

.random-message {
  width: 90%;
  border: 1px solid rgb(125, 42, 232);
  margin: 0 auto;
  border-radius: 5px;
  background-color: #fff;
}

.send-msg {
  width: 90%;
  margin: 0 auto;
  text-align: right;
}

.btn-msg {
  margin-bottom: 20px;
}

.msg-title {
  display: flex;
  justify-content: center;
  align-items: center;
}
form textarea {
  resize: none;
}

.goodbye {
  width: 90%;
  max-width: 900px;
  margin: 200px auto 30px auto;
  text-align: center;
  -webkit-animation: 2s linear goodbye;
          animation: 2s linear goodbye;
}

@-webkit-keyframes goodbye {
  0% {
    opacity: 0;
    margin-top: 250px;
  }
  100% {
    opacity: 1;
  }
}

@keyframes goodbye {
  0% {
    opacity: 0;
    margin-top: 250px;
  }
  100% {
    opacity: 1;
  }
}
.gb-btns .btn {
  width: 100px;
  padding: 10px 5px;
  -webkit-animation: 3s linear buttons;
          animation: 3s linear buttons;
}
@-webkit-keyframes buttons {
  0% {
    visibility: hidden;
    opacity: 0;
  }
  100% {
    visibility: visible;
    opacity: 1;
  }
}
@keyframes buttons {
  0% {
    visibility: hidden;
    opacity: 0;
  }
  100% {
    visibility: visible;
    opacity: 1;
  }
}
.gb-btns {
  display: flex;
  justify-content: space-evenly;
}

